<template>
  <main class="box-border h-full min-h-[calc(100vh-84px)] w-full pb-[160px]">
    <C-Back-Nav :title="`${GETTER_MECHANTNAME !== '' ? GETTER_MECHANTNAME + ' - ' : ''}行事曆`"></C-Back-Nav>
    <section class="relative z-0 mt-[52px] p-3">
      <div class="relative z-10 my-4 flex w-6 items-center justify-between">
        <font-awesome-icon icon="fa-regular fa-filter-list" class="text-2xl" @click="isActiveFilter = !isActiveFilter"/>
      </div>

      <!-- 不同的檢視模式 -->
      <!-- 月模式 -->
      <MonthView
        v-if="showType==='0'"
        v-model="monthlyOrdersDate"
        :attributes="formatResult"
        :seletedCategories="seleted"
        :manaulUpdateFormResult="manaulUpdateFormResult"
        class="animate__animated animate__fadeInUp"
      ></MonthView>

      <!-- 周模式 -->
     <WeekView
        v-if="showType==='1'"
        v-model="period"
        :weekLst="weekLst"
        :attributes="restructureScheduleTableWithTimeList"
        :isRegularHoliday="restructureScheduleTableWithisRegualrHoliday"
        :manaulUpdateFormResult="manaulUpdateFormResult"
        class="animate__animated animate__fadeInUp"
      ></WeekView>

      <!-- 單日模式 -->
      <DayView
        v-if="showType==='2'"
        v-model="today"
        :weekLst="weekLst"
        :manaulUpdateFormResult="manaulUpdateFormResult"
        :attributes="restructureScheduleTableWithTimeListOfDay"
        :isRegularHoliday="restructureScheduleTableWithisRegualrHoliday"
        class="animate__animated animate__fadeInUp"
      ></DayView>

      <!-- 打開小鈴鐺 @@? 不是啦是打開 打開新增預約和檢視行事曆的tooltip -->
      <div class="fixed bottom-[100px] right-3 z-10 text-right">
        <font-awesome-icon icon="fa-regular fa-circle-plus" class=" text-[78px]" @click="isDirect = !isDirect"/>
      </div>
    </section>
    <!-- 新增預約和檢視行事曆的tooltip -->
    <C-Loading-Overlay v-model="isDirect" @doFunction="isDirect = !isDirect" :layerIndex="10"></C-Loading-Overlay>
    <div class="animate__animated animate__fadeInUp fixed inset-x-6 bottom-40 !z-10 mx-auto flex justify-end content-['']"  v-if="isDirect">
      <div class="relative w-[150px] rounded-md bg-white after:absolute after:right-[calc(10%-0px)] after:bottom-[-20px] after:inline-block after:rotate-180 after:border-[10px] after:border-solid after:border-transparent after:border-b-white after:content-['']">
        <div class="py-2 text-right text-black">
          <div class="w-full cursor-pointer text-sm">
            <p class=" m-[8px] text-left text-2xl" @click="doRoute({name: 'ScheduleBreak-Add'}),isDirect = !isDirect" @keydown="doRoute({name: 'ScheduleBreak-Add'}),isDirect = !isDirect">新增預約</p>
            <p class=" m-[8px] text-left text-2xl" @click="doRoute({name: 'ScheduleBreak-List'}),isDirect = !isDirect" @keydown="doRoute({name: 'ScheduleBreak-List'}),isDirect = !isDirect">我的排休</p>
            <!-- <p class=" m-[8px] text-left" @click="doRoute({name: 'ScheduleBreak-Duotone'}),isDirect = !isDirect" @keydown="doRoute({name: 'ScheduleBreak-Duotone'}),isDirect = !isDirect">排休清單</p> -->
          </div>
        </div>
      </div>
    </div>

    <!-- 過濾條件 -->
    <C-Loading-Overlay v-model="isActiveFilter" @doFunction="isActiveFilter = !isActiveFilter" :layerIndex="10"></C-Loading-Overlay>
    <transition name="fromleft">
      <div v-show="isActiveFilter">
        <div class="fixed inset-0 top-[180px] z-20 grid h-[calc(100%-180px)] w-full grid-cols-1 grid-rows-[37px_minmax(0,1fr)_62px] bg-white">
          <p class="border-b border-solid border-primary p-2 text-sm">顯示行事曆</p>
          <div class="p-[14px]">
            <p class="mt-4 text-left font-semibold">瀏覽模式</p>
            <div class="mb-4">
              <div class="my-2 flex items-center gap-2">
                <input name="showType" v-model="showType" type="radio" value="0">
                <label class="w-11/12 text-left">月曆模式</label>
              </div>
              <div class="my-2 flex items-center gap-2">
                <input name="showType" v-model="showType" type="radio" value="1">
                <label class="w-11/12 text-left">週模式</label>
              </div>
              <div class="my-2 flex items-center gap-2">
                <input name="showType" v-model="showType" type="radio" value="2">
                <label class="w-11/12 text-left">日模式</label>
              </div>
            </div>

            <p class="text-left font-semibold">選擇類別</p>
            <div class="mb-8">
              <div class="flex items-center gap-2" v-if="isAuthToAll && isAuthority('全店行事曆')">
                <C-Checkbox-Full v-model="seletedAll" :inputValue="'my'" class="!w-1/12"></C-Checkbox-Full>
                <p class="w-11/12 text-left">全店行事曆</p>
              </div>
              <div class="flex items-center gap-2">
                <C-Checkbox-Full v-model="seleted" :inputValue="'my'" class="!w-1/12"></C-Checkbox-Full>
                <p class="w-11/12 text-left">指定預約</p>
              </div>
              <div class="flex items-center gap-2">
                <C-Checkbox-Full v-model="seleted" :inputValue="'others'" class="!w-1/12"></C-Checkbox-Full>
                <p class="w-11/12 text-left">不指定預約</p>
              </div>
              <div class="flex items-center gap-2">
                <C-Checkbox-Full v-model="seleted" :inputValue="'break'" class="!w-1/12"></C-Checkbox-Full>
                <p class="w-11/12 text-left">排休</p>
              </div>
              <div class="flex items-center gap-2">
                <C-Checkbox-Full v-model="seleted" :inputValue="'regularHoliday'" class="!w-1/12"></C-Checkbox-Full>
                <p class="w-11/12 text-left">公休</p>
              </div>
            </div>

          </div>
          <div class="my-3 mx-[14px] flex justify-around gap-3 ">
            <div class="w-full bg-second px-[14px] py-2 text-sm text-white" @click="isActiveFilter = !isActiveFilter" @keypress="isActiveFilter = !isActiveFilter"><span>關閉</span></div>
          </div>
        </div>
      </div>
    </transition>

  </main>
</template>

<script>
import { mapGetters, mapState } from 'vuex';
import MonthView from '@/components/Home/scheduleBreak/monthView.vue';
import WeekView from '@/components/Home/scheduleBreak/weekView.vue';
import DayView from '@/components/Home/scheduleBreak/dayView.vue';

export default {
  name: 'order-index',
  description: '行事曆的首頁',
  metaInfo: {
    title: '名留',
    titleTemplate: '%s | 行事曆',
  },
  components: {
    MonthView,
    WeekView,
    DayView,
  },
  data() {
    return {
      // 全店預約
      seletedAll: false,
      // 打開過濾條件
      isActiveFilter: false,
      // 打開新增預約和檢視行事曆的tooltip
      isDirect: false,
      // 是否有權限可以檢視全店的預約內容
      isAuthToAll: false,
      // 打設計師預約清單的 result
      designerBookingList: [],
      // 選擇類別，看能看到那些資料
      seleted: ['my', 'others', 'break', 'regularHoliday'],
      // 不同的檢視模式 '0' 月模式 '1' 周模式 '2' 日模式
      showType: '0',
      // 用於周模式中切換 周模式的時間，得以每周每周切換
      period: 0,
      // 月模式月份的切換
      monthlyOrdersDate: {
        start: this.$dayjs(new Date()).startOf('month').valueOf(),
        end: this.$dayjs(new Date()).endOf('month').valueOf(),
      },
      // 日模式日期的切換
      today: this.$dayjs().format('YYYY-MM-DD'),
      // 用於月模式的過濾後的內容
      attributes: [],
      formatResult: [],
      timeListOfBookings: [],
    };
  },
  mounted() {
    this.init();
  },
  methods: {
    // 初始化
    async init() {
      await this.getIsMerchantScheduleDisplay();
      await this.getDesignerBookingTimesWithRange();
    },

    // 取得是否有權限檢視全部的預約
    async getIsMerchantScheduleDisplay() {
      const resp = await this.$api.getIsMerchantScheduleDisplay();
      const { data, errors } = resp.data;
      if (errors) return;
      this.isAuthToAll = data.getIsMerchantScheduleDisplay;
    },

    // 獲取所有設計師的當月的所有內容
    async getAllBookingTimesWithRange() {
      const actions = new Map([
        ['0', { startDate: this.$dayjs(this.monthlyOrdersDate.start).valueOf(), endDate: this.$dayjs(this.monthlyOrdersDate.end).valueOf() }],
        ['1', { startDate: this.$dayjs(this.weekLst[0]).valueOf(), endDate: this.$dayjs(this.weekLst[6]).valueOf() }],
        ['2', { startDate: this.$dayjs(this.$dayjs(this.today).format('YYYY-MM-DD 00:00:00')).valueOf(), endDate: this.$dayjs(this.$dayjs(this.today).format('YYYY-MM-DD 23:59:59')).valueOf() }],
      ]);

      const submitForm = {
        storeId: this.currentMechantId,
        dateRange: {
          startDate: actions.get(this.showType).startDate,
          endDate: actions.get(this.showType).endDate,
        },
      };
      const resp = await this.$api.getAllBookingTimesWithRange(submitForm);
      const { data, errors } = resp.data;
      if (errors) return;
      this.formatResult = [];

      const flatGetAllBookingTimesWithRange = data.getAllBookingTimesWithRange.flatMap((item) => ((Array.isArray(item.schedule) && item.schedule.length > 0)
        ? [...item.schedule.map((innerItem) => ({ ...innerItem, designer: item.designer }))] // 因為要知道是誰的公休排休所以重新塞
        : []));
      this.timeListOfBookings = flatGetAllBookingTimesWithRange;
      this.formatResult = [...this.ordersList(flatGetAllBookingTimesWithRange), ...this.HolidayList(flatGetAllBookingTimesWithRange), ...this.HolidayTimeRangeList(flatGetAllBookingTimesWithRange), ...this.dayOffList(flatGetAllBookingTimesWithRange)];
    },

    // 獲取設計師的當月的所有內容
    async getDesignerBookingTimesWithRange() {
      const actions = new Map([
        ['0', { startDate: this.$dayjs(this.monthlyOrdersDate.start).valueOf(), endDate: this.$dayjs(this.monthlyOrdersDate.end).valueOf() }],
        ['1', { startDate: this.$dayjs(this.weekLst[0]).valueOf(), endDate: this.$dayjs(this.weekLst[6]).valueOf() }],
        ['2', { startDate: this.$dayjs(this.$dayjs(this.today).format('YYYY-MM-DD 00:00:00')).valueOf(), endDate: this.$dayjs(this.$dayjs(this.today).format('YYYY-MM-DD 23:59:59')).valueOf() }],
      ]);

      const submitForm = {
        designerId: this.userInfo.user.id,
        storeId: this.currentMechantId,
        dateRange: {
          startDate: actions.get(this.showType).startDate,
          endDate: actions.get(this.showType).endDate,
        },
      };
      const resp = await this.$api.getDesignerBookingTimesWithRange(submitForm);
      const { data, errors } = resp.data;
      if (errors) return;
      const getDesignerBookingTimesWithRange = data.getDesignerBookingTimesWithRange.map((item) => ({ ...item, designer: this.designer }));
      this.timeListOfBookings = getDesignerBookingTimesWithRange;
      this.formatResult = [];
      this.formatResult = [...this.ordersList(getDesignerBookingTimesWithRange), ...this.HolidayList(getDesignerBookingTimesWithRange), ...this.HolidayTimeRangeList(getDesignerBookingTimesWithRange), ...this.dayOffList(getDesignerBookingTimesWithRange)];
    },

    // 處理並獲取全日公休
    HolidayList(OriginalArr) {
      console.log(OriginalArr);
      let holiday = [];
      if (Array.isArray(OriginalArr) && OriginalArr.length > 0) {
        holiday = OriginalArr.flatMap((item, index) => (
          item.isRegularHoliday
            ? [{
              key: `HolidayList-${index}`,
              customData: {
                title: `${item.designer.name}公休`, // 全日
                class: 'bg-blue-600 text-white',
                type: 'regularHoliday',
                designer: item.designer,
                bookingTimes: item.bookingTimes,
              },
              dates: item.date,
            }]
            : []
        ));
      }
      return holiday;
    },
    // 處理並獲取時段公休
    HolidayTimeRangeList(OriginalArr) {
      /**
       * bookingTimesListWrap: 取得每一天，每一天裡面有所有的時段
       * bookingTimesListFlat: 朝狀結構扁平化，變成所有的時段的陣列
       * ordersList: 取得時段公休的時段，判斷條件為 時段物件的 isRegularHoliday 或者 statusComment 為 該時段公休
       * resultArr: 重新map出我們要的物件
       */
      let [bookingTimesListWrap = [], bookingTimesListFlat = [], ordersList = [], resultArr = []] = [];

      bookingTimesListWrap = OriginalArr.flatMap((designerBookingListItem) => (Array.isArray(designerBookingListItem.bookingTimes) && designerBookingListItem.bookingTimes.length > 0
      // 重新塞設計師 來源是 透過 getDesignerBookingTimesWithRange 或者 getAllBookingTimesWithRange 獲取或判斷而來
        ? [designerBookingListItem.bookingTimes.map((item) => ({ ...item, designer: designerBookingListItem.designer }))]
        : []));

      bookingTimesListFlat = bookingTimesListWrap.flatMap((bookingTimesListWrapItem) => ([...bookingTimesListWrapItem]));

      ordersList = bookingTimesListFlat.flatMap((bookingTimesListFlatItem) => ((bookingTimesListFlatItem.statusComment && bookingTimesListFlatItem.statusComment === '該時段公休') ? [bookingTimesListFlatItem] : []));

      resultArr = ordersList.map((item, index) => ({
        key: `HolidayTimeRangeList-${index}`,
        customData: {
          title: `${item.designer.name}公休${this.$dayjs(item.time).format('HH:mm')}`, // 時段
          class: 'bg-blue-400 text-white',
          type: 'regularHoliday',
          designer: item.designer,
        },
        dates: item.time,
      }));

      return resultArr;
    },

    // 處理並獲取時段排休
    dayOffList(OriginalArr) {
      /**
       * bookingTimesListWrap: 取得每一天，每一天裡面有所有的時段
       * bookingTimesListFlat: 朝狀結構扁平化，變成所有的時段的陣列
       * ordersList: 取得時段排休的時段，判斷條件為 時段物件的 isRegularHoliday 或者 statusComment 為 該時段排休
       * resultArr: 重新map出我們要的物件
       */
      let [bookingTimesListWrap = [], bookingTimesListFlat = [], ordersList = [], resultArr = []] = [];

      bookingTimesListWrap = OriginalArr.flatMap((designerBookingListItem) => (Array.isArray(designerBookingListItem.bookingTimes) && designerBookingListItem.bookingTimes.length > 0
      // 重新塞設計師 來源是 透過 getDesignerBookingTimesWithRange 或者 getAllBookingTimesWithRange 獲取或判斷而來
        ? [designerBookingListItem.bookingTimes.map((item) => ({ ...item, designer: designerBookingListItem.designer }))]
        : []));

      bookingTimesListFlat = bookingTimesListWrap.flatMap((bookingTimesListWrapItem) => ([...bookingTimesListWrapItem]));

      ordersList = bookingTimesListFlat.flatMap((bookingTimesListFlatItem) => ((bookingTimesListFlatItem.statusComment && bookingTimesListFlatItem.statusComment === '該時段排休') ? [bookingTimesListFlatItem] : []));

      resultArr = ordersList.map((item, index) => ({
        key: `dayOffList-${index}`,
        customData: {
          title: `${item.designer.name}排休${this.$dayjs(item.time).format('HH:mm')}`, // 時段
          class: 'bg-red-600 text-white',
          type: 'break',
          designer: item.designer,
        },
        dates: item.time,
      }));
      return resultArr;
    },

    // 處理並獲所有訂單 // 訂單無須再特殊處理設計師一層傳一層 因為訂單本身就有設計師
    ordersList(OriginalArr) {
      /**
       * bookingTimesListWrap: 取得每一天，每一天裡面有所有的時段
       * bookingTimesListFlat: 朝狀結構扁平化，變成所有的時段的陣列
       * ordersList: 取得時段排休的時段，判斷條件為 時段物件的 isRegularHoliday 或者 statusComment 為 該時段排休
       * resultArr: 重新map出我們要的物件
       */
      let [bookingTimesListWrap = [], bookingTimesListFlat = [], ordersList = [], resultArr = []] = [];

      bookingTimesListWrap = OriginalArr.flatMap((designerBookingListItem) => (Array.isArray(designerBookingListItem.bookingTimes) && designerBookingListItem.bookingTimes.length > 0
      // 重新塞設計師 來源是 透過 getDesignerBookingTimesWithRange 或者 getAllBookingTimesWithRange 獲取或判斷而來
        ? [designerBookingListItem.bookingTimes.map((item) => ({ ...item, designer: designerBookingListItem.designer }))]
        : []));

      bookingTimesListFlat = bookingTimesListWrap.flatMap((bookingTimesListWrapItem) => ([...bookingTimesListWrapItem]));

      ordersList = bookingTimesListFlat.flatMap((bookingTimesListFlatItem) => (Array.isArray(bookingTimesListFlatItem.orders) && bookingTimesListFlatItem.orders.length > 0
      // 自己的不指定與指定顏色不同，同時他人的不指定與指定顏色相同所以繼承上層的設計師，上層的設計師如果是全部的設計師會是迴圈中的設計師
        ? [...bookingTimesListFlatItem.orders.map((item) => ({ ...item, propsDesigner: bookingTimesListFlatItem.designer }))]
        : []));
      /**
       * 自己的不指定與指定顏色不同，同時他人的不指定與指定顏色相同
       */
      resultArr = ordersList.map((item) => {
        let identifyClass = '';
        if (item.propsDesigner.id === this.userInfo.user.id) {
          // 自己的指定與不指定
          identifyClass = item.designer && item.designer.id === this.userInfo.user.id ? 'bg-second text-white' : 'bg-green-500 text-white';
        } else {
          // 別人的指定與不指定
          identifyClass = 'bg-slate-200 text-green-600';
        }
        return {
          key: item.id,
          customData: {
            title: item.customer.name || '--', // 這邊當排休之後再預約就會沒有名子...
            class: identifyClass,
            type: item.isDesignate ? 'my' : 'others',
          },
          dates: item.date,
        };
      });

      return resultArr;
    },

    // 切換路由
    doRoute(item) {
      this.$router.push({ name: item.name });
    },
    async manaulUpdateFormResult() {
      if (this.seletedAll) {
        await this.getAllBookingTimesWithRange();
      } else {
        await this.getDesignerBookingTimesWithRange();
      }
    },
  },
  activated() {
    this.init();
  },
  computed: {
    ...mapState('userInfo', ['userInfo', 'currentMechantId', 'userAuthorizeList']),
    ...mapGetters('userInfo', ['GETTER_MECHANTNAME']),
    isAuthority() {
      return (name) => this.userAuthorizeList.find((item) => item.pageName === name && item.authority);
    },
    designer() {
      return {
        id: this.userInfo.user.id,
        name: this.userInfo.user.name,
      };
    },

    restructureScheduleTableWithisRegualrHoliday() {
      return this.formatResult.filter((item) => item.key.includes('HolidayList'));
    },
    restructureScheduleTableWithTimeList() {
      let timeList = [];
      let uniqueTimeList = [];
      let restructureScheduleTableWithTimeList = [];
      timeList = this.timeListOfBookings.flatMap((designerBookingListItem) => (!designerBookingListItem.bookingTimes ? []
        : designerBookingListItem.bookingTimes.flatMap((bookingTimesItem) => (!bookingTimesItem ? [] : [this.$dayjs(bookingTimesItem.time).isValid() ? this.$dayjs(bookingTimesItem.time).format('HH:mm') : []]))));
      // 時段去重複
      uniqueTimeList = [...new Set(timeList)];

      const filterSeletedCategories = this.formatResult.filter((item) => {
        if (this.seleted.length === 0) {
          return false;
        }
        return this.seleted.includes(item.customData.type);
      });

      restructureScheduleTableWithTimeList = uniqueTimeList.flatMap((HHmm) => {
        let [sunDate, monDate, tueDate, wedDate, thuDate, friDate, satDate] = [];
        sunDate = Array.isArray(filterSeletedCategories) ? filterSeletedCategories.filter((item) => this.$dayjs(item.dates).isBetween(this.weekLst[0], this.weekLst[1]) && this.$dayjs(item.dates).format('HH:mm') === HHmm) : '';
        monDate = Array.isArray(filterSeletedCategories) ? filterSeletedCategories.filter((item) => this.$dayjs(item.dates).isBetween(this.weekLst[1], this.weekLst[2]) && this.$dayjs(item.dates).format('HH:mm') === HHmm) : '';
        tueDate = Array.isArray(filterSeletedCategories) ? filterSeletedCategories.filter((item) => this.$dayjs(item.dates).isBetween(this.weekLst[2], this.weekLst[3]) && this.$dayjs(item.dates).format('HH:mm') === HHmm) : '';
        wedDate = Array.isArray(filterSeletedCategories) ? filterSeletedCategories.filter((item) => this.$dayjs(item.dates).isBetween(this.weekLst[3], this.weekLst[4]) && this.$dayjs(item.dates).format('HH:mm') === HHmm) : '';
        thuDate = Array.isArray(filterSeletedCategories) ? filterSeletedCategories.filter((item) => this.$dayjs(item.dates).isBetween(this.weekLst[4], this.weekLst[5]) && this.$dayjs(item.dates).format('HH:mm') === HHmm) : '';
        friDate = Array.isArray(filterSeletedCategories) ? filterSeletedCategories.filter((item) => this.$dayjs(item.dates).isBetween(this.weekLst[5], this.weekLst[6]) && this.$dayjs(item.dates).format('HH:mm') === HHmm) : '';
        satDate = Array.isArray(filterSeletedCategories) ? filterSeletedCategories.filter((item) => this.$dayjs(item.dates).isBetween(this.$dayjs(this.weekLst[6]).format('YYYY-MM-DD 00:00:00'), this.weekLst[6]) && this.$dayjs(item.dates).format('HH:mm') === HHmm) : '';
        return [{
          time: HHmm,
          date: [sunDate, monDate, tueDate, wedDate, thuDate, friDate, satDate],
        }];
      });
      return restructureScheduleTableWithTimeList;
    },
    restructureScheduleTableWithTimeListOfDay() {
      let timeList = [];
      let uniqueTimeList = [];
      let restructureScheduleTableWithTimeList = [];
      timeList = this.timeListOfBookings.flatMap((designerBookingListItem) => (!designerBookingListItem.bookingTimes ? []
        : designerBookingListItem.bookingTimes.flatMap((bookingTimesItem) => (!bookingTimesItem ? [] : [this.$dayjs(bookingTimesItem.time).isValid() ? this.$dayjs(bookingTimesItem.time).format('HH:mm') : []]))));
      // 時段去重複
      uniqueTimeList = [...new Set(timeList)];

      const filterSeletedCategories = this.formatResult.filter((item) => {
        if (this.seleted.length === 0) {
          return false;
        }
        return this.seleted.includes(item.customData.type);
      });

      restructureScheduleTableWithTimeList = uniqueTimeList.flatMap((HHmm) => {
        let [sunDate] = [];
        sunDate = Array.isArray(filterSeletedCategories) ? filterSeletedCategories.filter((item) => this.$dayjs(item.dates).isBetween(this.$dayjs(this.today).format('YYYY-MM-DD 00:00:00'), this.$dayjs(this.today).format('YYYY-MM-DD 23:59:59')) && this.$dayjs(item.dates).format('HH:mm') === HHmm) : '';
        return [{
          time: HHmm,
          date: [sunDate],
        }];
      });
      return restructureScheduleTableWithTimeList;
    },
    weekLst() {
      return [
        this.$dayjs(this.$dayjs().format('YYYY-MM-DD 00:00:00')).day(this.period),
        this.$dayjs(this.$dayjs().format('YYYY-MM-DD')).day(this.period + 1),
        this.$dayjs(this.$dayjs().format('YYYY-MM-DD')).day(this.period + 2),
        this.$dayjs(this.$dayjs().format('YYYY-MM-DD')).day(this.period + 3),
        this.$dayjs(this.$dayjs().format('YYYY-MM-DD')).day(this.period + 4),
        this.$dayjs(this.$dayjs().format('YYYY-MM-DD')).day(this.period + 5),
        this.$dayjs(this.$dayjs().format('YYYY-MM-DD 23:59:59')).day(this.period + 6),
      ];
    },
  },
  watch: {
    seletedAll() {
      if (this.seletedAll) {
        this.getAllBookingTimesWithRange();
      } else {
        this.getDesignerBookingTimesWithRange();
      }
    },
    monthlyOrdersDate: {
      deep: true,
      handler() {
        this.manaulUpdateFormResult();
      },
    },
    weekLst: {
      deep: true,
      handler() {
        this.manaulUpdateFormResult();
      },
    },
    today: {
      deep: true,
      handler() {
        if (this.seletedAll) {
          this.getAllBookingTimesWithRange();
        } else {
          this.getDesignerBookingTimesWithRange();
        }
      },
    },
    showType() {
      if (this.seletedAll) {
        this.getAllBookingTimesWithRange();
      } else {
        this.getDesignerBookingTimesWithRange();
      }
    },
  },
};
</script>

